eading causes of death, including stroke, heart disease, lung cancer, and respiratory diseases, such as asthma.3 In the chart we see that it is one of the leading risk factors for death globally.\n        ")]),_c('div',{staticClass:"text"},[_vm._v("\n          According to the Global Burden of Disease study 4506193 people died prematurely as a result of outdoor air pollution in the latest year.\n        ")]),_c('div',{staticClass:"text"},[_c('span',{staticClass:"bold"},[_vm._v("7.8% of global deaths are attributed to outdoor air pollution")])]),_c('div',{staticClass:"text"},[_vm._v("\n          Outdoor air pollution was attributed to an estimated 4506193 deaths in the latest year. This means that outdoor air pollution was responsible for 7.8% of global deaths.\n        ")]),_c('div',{staticClass:"text"},[_vm._v("\n          In some countries, it accounts for 10% of deaths, or higher.          \n        ")]),_c('div',{staticClass:"text"},[_vm._v("\n          In the map here we see the share of annual deaths attributed to outdoor air pollution across the world.      \n        ")]),_c('div',{staticClass:"text"},[_vm._v("\n          When we compare the share of deaths attributed to outdoor air pollution either over time or between countries, we are not only comparing the extent of outdoor air pollution, but its severity in the context of other risk factors for death. Air pollution's share does not only depend on how many die prematurely from it, but what else people are dying from and how this is changing.\n        ")]),_c('div',{staticClass:"text"},[_c('span',{staticClass:"bold"},[_vm._v("Concentrations of air pollution")])]),_c('div',{staticClass:"text"},[_vm._v("\n          Alongside ozone pollution, the main contributor to poor health from air pollution is particulate matter. In particular, very small particles of matter termed PM2.5, which are particles with a size (diameter) of less than 2.5 micrometres (um). Smaller particles tend to have more adverse health effects because they can enter airways and affect the respiratory system.\n        ")]),_c('div',{staticClass:"text"},[_vm._v("\n          How does exposure to PM2.5 vary across the world? In the map we see the distribution of the population-weighted mean exposure to PM2.5 each year. We see a greater than ten-fold difference in exposure between countries.\n        ")]),_c('div',{staticClass:"text"},[_vm._v("\n          Pollution exposure is high in many low-to-middle income countries across Africa and Asia. In particular, concentrations are very high across North Africa, partly owed to drier conditions with more sand and dust sources.\n        ")]),_c('div',{staticClass:"text"},[_vm._v("\n          There, exposure can reach as high as 200ug per cubic meter. Compare this with Sweden where exposure levels are 5ug/m3 一 40 times lower.\n        ")]),_c('div',{staticClass:"text"}),_c('div',{staticClass:"text small"},[_c('span',{staticClass:"bolder"},[_vm._v("Endnotes")]),_c('br'),_vm._v("\n          1. Murray, C. J, Aravkin, A. Y, Zheng, P., Abbafati, C., Abbas, K. M., Abbasi-Kangevari, M.,... & Borzouei, S. (2020). Globalburden of 87 risk factors in 204 countries and territories, 1990-2019:a systematic analysis for the Global Burden of Disease Study 2019, The Lancet, 396(10258), 1223-1249.\n          "),_c('br'),_vm._v("\n          2. Murray, C. J., Aravkin, A. Y., Zheng, P., Abbafati, C., Abbas, K. M., Abbasi-Kangevari, M.,... & Borzouei, S. (2020). Globalburden of 87 risk factors in 204 countries and territories, 1990-2019:a systematicanalysis for the Global Burden of Disease Study 2019. The Lancet, 396(10258), 1223-1249.\n          "),_c('br'),_vm._v("\n          3. WHO (2018) - Fact sheet - Ambient air quality and health. Updated May 2018. Online here\n          "),_c('br'),_vm._v("\n          4. WHO (2018) - Fact sheet Ambient air quality and health. Updated May 2018. Online here,\n          "),_c('br'),_vm._v("\n          5. World Health Organization.(2006) WHO Air qu